.switch-icon{
    background-color: white;
 }
.drop-down{
    width: 80px;
}
.frm1{
    background-color: #09131b;
}
.modal{
    background-color: #09131b;
}
.content{
    min-height: 65vh;
    height: auto;
    background-color: #09131b;
    color: white;
    overflow: inherit;
}
.ant-table-row{
    background-color: #09131b;
    color: white;
}
.ant-spin-container{
    padding: 10px;
}
.ant-table-tbody > tr:hover > td{
    background: none !important;
}
.ant-table-head{
    background-color: #09131b;
    color: white;
}
.card-body{
    background-color: #09131b;
    color: white;
}
.ant-progress{
    align-items: center;
    background-color: #09131b;
    color: aliceblue;
}
.card-header{
    background-color: #09131b;
    color: white;
}
.bar{
    flex-direction: column;
    padding: 20px;
}
.inside{
    background-color: #09131b;
}
.ant-progress-inner{
    width: 250px;
}




@media screen and (min-width: 2000px) {
    .btn1,.btn2,.btn3 {
      font-size: 37px;
      line-height: 67px;
    }
    * {
        font-size: 12px;
        width: inherit;
    }
  }
  
  @media screen and (max-width: 1150px) {
    * {
        font-size: 12px;
    }
    .content{
      min-height: 100vh;
    }
  }
  
  @media screen and (max-width: 850px) {
    .inside{
      flex-direction: row;
    }
    .content{
      min-height: 100vh;
    }
  }
  
  @media screen and (max-width: 650px) {
    * {
        font-size: 12px;
        width: auto;
    }
    .content{
      min-height: 100vh;
    }
    .filters {
      justify-content: inherit;
      justify-content: space-between;
    }
    
  }
  
  @media screen and (max-width: 450px) {
    *{
      font-size: 12px;
      width: auto;
    }
    .filters {
        justify-content: inherit;
        justify-content: space-between;
    }
    .container-fluid{
      width: 60vh;
      padding: 2vh;
    }
    .navbar-brand{
      padding-right: 3vh;
    }
    .content{
      width: 60vh;
      min-height: 100vh;
    }
    .footer{
      width: 60vh;
    }
  }

.bg{
    background-color: #09131b;
}