.register-page{
    background-color: #09131b;
    height: 100vh;
    width: auto;
}

.emoji{
    margin-left: 120px;
    align-items: center;
    height: 100px;
    width: 100px;
    margin-bottom: 20px;
    display: inline-block;
    transition: transform 0.5s ease-in-out;
}
@keyframes shake{
    0%,100%{
        transform:rotate(0deg);
    }
    25%{
        transform:rotate(20deg);
    }
    50%{
        transform:rotate(0deg);
    }
    75%{
        transform:rotate(-20deg);
    }
}

.shake{
    animation: shake 0.5s ease-in-out;
}

h1{
    font-family: 'Times New Roman', Times, serif;
    margin-left: 30px;
    margin-bottom: 50px;
    background-image: none;
    color: white;
    margin-right: 25px;
}
.frm .ant-form-item-label label{
    color: white;
    font-family: "Open Sans", sans-serif;
}

.btn2{
    margin-top: 20px;
    margin-left: 100px;
    font-family: "Open Sans", sans-serif;
    background-color: #136c6c;
    color: white;
    font-weight: 500;
    letter-spacing: 0.04em;
    line-height: 28px;
    font-size: 14px;
    padding: 0.5rem 1.5rem;
    border-radius: 15px;
    border: none;
    outline: none;
    cursor: pointer;
}
.btn2:hover{
    background-color: white;
    color: #0C0C0C;
}
.ipt{
    font-family: "Open Sans", sans-serif;
    width: 300px;
    border: 0;
    border-bottom: 4px solid gray;
    font-size: 1rem;
    color: black;
    padding: 7px 2px;
    background: transparent;
    transition: border-color 0.2s;
    background-image: none;
}
.linklogin{
    color: white;
    margin-left: 40px;
}
.linklogin:hover{
    color: #136c6c;
}

