*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  
}

.content{
  height: 100vh;
  box-shadow: 0 0 10px grey;
}

.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.filters{
  display: flex;
  background-color: #09131b;
  align-items: center;
  justify-content: space-between;
  margin-top: 0;
  padding: 15px 20px;
  box-shadow: 0 0 3px grey;
}

.switch-icon{
  border: 1px solid #09131b;
  border-radius: 5px;
  padding: 10px 15px;
}
.anticon svg{
  font-size: 20px;
  cursor: pointer;
}
.active-icon{
  color: #09131b;
}
.inactive-icon{
  color: rgb(138, 130, 130);
}