.register-page{
    background-color: #09131b;
    height: 100vh;
    width: auto;
}

.emoji{
    align-items: center;
    height: 100px;
    width: 100px;
    margin-bottom: 20px;
    display: inline-block;
    transition: transform 0.5s ease-in-out;
}
@keyframes shake{
    0%,100%{
        transform:rotate(0deg);
    }
    25%{
        transform:rotate(20deg);
    }
    50%{
        transform:rotate(0deg);
    }
    75%{
        transform:rotate(-20deg);
    }
}

.shake{
    animation: shake 0.5s ease-in-out;
}

.frm .ant-form-item-label label{
    color: white;
    font-family: "Open Sans", sans-serif;
}

h1{
    font-family: 'Times New Roman', Times, serif;
    margin-left: 30px;
    margin-bottom: 50px;
    background-image: none;
    color: white;
    margin-right: 25px;
}
.ipt{
    font-family: "Open Sans", sans-serif;
    width: 100%;
    border: 0;
    border-bottom: 2px solid gray;
    outline: 0;
    font-size: 1.3rem;
    color: black;
    padding: 7px 0;
    background: transparent;
    transition: border-color 0.2s;

    &::placeholder {
        color: transparent;
    }

    &:placeholder-shown ~ .form__label {
        font-size: 1.3rem;
        cursor: text;
        top: 20px;
    }
}
.btn1{
    margin-top: 20px;
    margin-left: 120px;
    font-family: "Open Sans", sans-serif;
    background-color: #136c6c;
    color: white;
    font-weight: 500;
    letter-spacing: 0.04em;
    line-height: 28px;
    font-size: 14px;
    padding: 0.5rem 1.5rem;
    border-radius: 15px;
    border: none;
    outline: none;
    cursor: pointer;
}
.btn1:hover{
    background-color: white;
    color: #0C0C0C;
}
.link1{
    color: white;
    margin-left: 60px;
}
.link1:hover{
    color: #136c6c;
}




