.container-fluid{
    background-color: #09131b;
    width: auto;
    height: 12vh;
    align-items: center;
    color: white;
    padding-right: 25px;
    margin-top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.navbar-brand{
    color: white;
    font-size: 30px;
    font-family: 'Times New Roman', Times, serif;
}
.navbar-brand:hover{
    color: #136c6c;
}
.nav-link{
    color: white;
    font-size: 15px;
    padding-top: 25px;
    font-family: 'Times New Roman', Times, serif;
}
.nav-link:hover{
    color: #136c6c;
}
.btn3{
    margin-left: 20px;
    font-family: "Open Sans", sans-serif;
    background-color: #136c6c;
    color: white;
    font-weight: 500;
    letter-spacing: 0.04em;
    line-height: 28px;
    font-size: 14px;
    padding: 0.5rem 1.5rem;
    border-radius: 15px;
    border: none;
    outline: none;
    cursor: pointer;
}
.btn3:hover{
    background-color: white;
    color: #0C0C0C;
}
.footer{
    padding-top: 0;
    margin-top: 0;
    height: 10vh;
    left: 0;
    bottom: 0;
    height: 10vh;
    width: 100%;
    background-color: #0C0C0C;
    color: white;
    text-align: center;
}
.text-center{
    font-size: 20px;
    align-items: center;
    padding-top: 15px;
}
